/*  7.3. BUTTONS
    ------------------------------------------------------------------------- */
.btn {
    text-transform: uppercase;
    font-weight: 700;
    position: relative;
    overflow: hidden;
    display: inline-block;
    backface-visibility: hidden;
    float: right;

    border-radius: 25px;
